import * as React from 'react';
import clsx from 'clsx';
import { withStyles, WithStyles } from '@material-ui/core';
import scrollIntoView from 'scroll-into-view';
import Stepper from '@material-ui/core/Stepper';
import Step from '@material-ui/core/Step';
import StepLabel from '@material-ui/core/StepLabel';
import StepConnector from '@material-ui/core/StepConnector';
import { StepIconProps } from '@material-ui/core/StepIcon';
import FiberManualRecordOutlinedIcon from '@material-ui/icons/FiberManualRecordOutlined';
import FiberManualRecordIcon from '@material-ui/icons/FiberManualRecord';
import VisibilityDetector from 'react-visibility-detector';
import { get } from 'lodash';
import Loadable from '@loadable/component';
import Style from './style';
import {
	SustainabilityGreenContent,
	SustainabilityEnergyContent,
	SustainabilityWaterContent
} from '../../constants/resources';

const Tile = Loadable(() => import('../../components/Tile'));
const Card = Loadable(() => import('../../components/Card'));

interface State {
	step: string;
	stepping: boolean;
}
const steps = [
	{ name: 'Our Philosophy', id: 'philosophy' },
	{ name: 'Green Certification', id: 'green' },
	{ name: 'Energy Efficiency', id: 'energy' },
	{ name: 'Water Efficiency', id: 'water' }
];
const QontoConnector = withStyles({
	alternativeLabel: {
		left: 'calc(-50% + 16px)',
		right: 'calc(50% + 16px)',
		top: 8
	},
	active: {
		'& $line': {
			borderColor: '#979797'
		}
	},
	completed: {
		'& $line': {
			borderColor: '#979797'
		}
	},
	line: {
		borderColor: '#979797',
		borderTopWidth: 1,
		borderRadius: 1
	}
})(StepConnector);
interface Props extends WithStyles<typeof Style> {
	[name: string]: any;
}
/**
 * SocialOutreach page.
 * @param {any} props - Properties
 * @returns {React.Component} component.
 */
class SocialOutreach extends React.PureComponent<Props, State> {
	constructor(props: Props) {
		super(props);
		this.state = {
			step: 'overview',
			stepping: false
		};
	}

	componentDidMount() {
		// scrollIntoView(document.getElementById('home') as any);
	}

	takeToLink = (id) => {
		scrollIntoView(document.getElementById(id) as any, { align: { top: 0.08, left: 0 } });
		this.setState({ stepping: true, step: id });
	};

	isVisible = ({ visible, directionY }, step) => {
		const { step: activeStep, stepping } = this.state;
		if (!stepping) {
			if (visible && activeStep !== step && directionY !== 'down') {
				this.setState({ step });
			} else if (!visible) {
				if (directionY === 'down') {
					const currentStep = steps.findIndex((item) => item.id === activeStep) || 0;
					const stepName = get(steps[currentStep - 1], 'id', 'overview');
					this.setState({ step: stepName });
				}
			}
		} else {
			this.setState({ stepping: false });
		}
	};

	QontoStepIcon = (props: StepIconProps, id) => {
		const { classes } = this.props;
		const { active } = props;
		return (
			<div>
				{active ? (
					<FiberManualRecordIcon className={classes.smallFont} />
				) : (
					<span>
						<FiberManualRecordOutlinedIcon className={classes.smallFont} />
					</span>
				)}
			</div>
		);
	};

	getActiveStep = () => {
		const { step } = this.state;
		return steps.findIndex((item) => item.id === step);
	};

	/**
	 * Render method
	 * @returns {JSX.Element} Jsx.
	 */
	public render() {
		const { classes } = this.props;
		return (
			<>
				<div className={classes.content}>
					<VisibilityDetector onVisibilityChange={(e) => this.isVisible(e, 'philosophy')}>
						<span id="philosophy" />
						<div id="home">
							<Card
								name=""
								designation=""
								image="/images/Sustainability/sustainability.png"
								smallCard
							/>
						</div>
						<div className={classes.block}>
							<div className={classes.largerFont}>Sustainability at DMart</div>
							<p className={clsx(classes.section, classes.smallFont)}>
								At DMart, it is our constant endeavor to act responsibly towards
								environment and create a positive impact with our sustainability
								efforts. We continue to focus on reducing our carbon footprint by
								adopting several best practices in areas of construction,
								sustainable energy usage and reduced fresh water usage. We have
								taken several steps in our construction activities to reduce the
								impact on environment by using sustainable material. We have reduced
								fresh water usage by increasing sewage treatment plant installations
								and also monitor our energy consumption by working on efficient
								lighting, refrigeration and solar power. We are also constantly
								working with our suppliers to ensure reduction of plastic in
								packaged products, increased use of alternative packaging materials,
								reduction in emission of greenhouse gases, usage of sustainable
								products for manufacturing and other such sustainable practices. We
								want to make a holistic difference in this area, and believe that
								there is more work to be done.
							</p>
						</div>
						<div>
							<div className={classes.marginTop_16}>
								<Card
									name=""
									designation=""
									image="/images/Sustainability/philosophy.png"
									smallCard
									padding
								/>
							</div>
						</div>
					</VisibilityDetector>
					<VisibilityDetector onVisibilityChange={(e) => this.isVisible(e, 'green')}>
						<span id="green" />
						<div className={clsx(classes.marginTop_40, classes.block)}>
							<div className={classes.largeFont}>Green Building Certification</div>
							<p
								className={clsx(
									classes.marginTop_8,
									classes.section,
									classes.smallFont
								)}
							>
								We have included sustainability principles in our stores design and
								have consistently increased Green Building certifications for our
								stores. This certification is issued by the Indian Green Building
								Council (IGBC) and the US Green Building Council (USGBC). It is our
								constant endeavor to construct all our premises using sustainable
								and environment friendly practices. Over a third of our stores are
								covered under the Green Building initiative.
							</p>
							<div className={classes.smallFont}>
								Green Building parameters include:
							</div>
							<p className={clsx(classes.section, classes.smallFont)}>
								{SustainabilityGreenContent.map((item, i) => (
									<Tile item={item} />
								))}
							</p>
						</div>
					</VisibilityDetector>
					<span id="energy" />
					<div className={classes.marginTop_40}>
						<Card
							name=""
							designation=""
							image="/images/Sustainability/EnergyEfficiency.png"
							smallCard
						/>
					</div>
					<VisibilityDetector onVisibilityChange={(e) => this.isVisible(e, 'energy')}>
						<div className={clsx(classes.marginTop_0, classes.block)}>
							<div className={classes.largeFont}>Energy Efficiency</div>
							<p
								className={clsx(
									classes.marginTop_8,
									classes.section,
									classes.smallFont
								)}
							>
								We are taking significant steps towards renewable energy use through
								multiple solutions.
							</p>
							<p className={clsx(classes.section, classes.smallFont)}>
								{SustainabilityEnergyContent.map((item, i) => (
									<Tile item={item} />
								))}
							</p>
						</div>
					</VisibilityDetector>
					<span id="water" />
					<div className={classes.marginTop_40}>
						<Card
							name=""
							designation=""
							image="/images/Sustainability/WaterEfficiency.png"
							smallCard
						/>
					</div>
					<VisibilityDetector onVisibilityChange={(e) => this.isVisible(e, 'water')}>
						<div className={clsx(classes.marginTop_8, classes.block)}>
							<div className={classes.largeFont}>Water Efficiency</div>
							<p
								className={clsx(
									classes.marginTop_8,
									classes.section,
									classes.smallFont
								)}
							>
								We are highly conscious of our water usage and take all steps to
								conserve it. We strive to minimize water wastage and maximize
								recycling.
							</p>
							<p className={clsx(classes.section, classes.smallFont)}>
								{SustainabilityWaterContent.map((item, i) => (
									<Tile item={item} />
								))}
							</p>
						</div>
					</VisibilityDetector>
				</div>
				<div className={classes.stepperBlock}>
					<Stepper
						alternativeLabel
						activeStep={this.getActiveStep()}
						connector={<QontoConnector />}
						orientation="horizontal"
						classes={{ root: classes.stepperRoot }}
					>
						{steps.map((item) => (
							<Step key={item.id}>
								<StepLabel
									onClick={() => this.takeToLink(item.id)}
									StepIconComponent={(props) =>
										this.QontoStepIcon(props, item.id)
									}
									classes={{
										labelContainer: classes.stepperLabel,
										label: classes.alternativeLabel
									}}
								>
									{item.name}
								</StepLabel>
							</Step>
						))}
					</Stepper>
				</div>
			</>
		);
	}
}

export default withStyles(Style, { name: 'DmartCorpFreshSocialOutreach' })(SocialOutreach);
